
















import { Component, Vue } from "vue-property-decorator";
import { TweenMax, TimelineMax } from "gsap";

@Component
export default class MusicSchool extends Vue {
  mounted() {
    const timeline = new TimelineMax();
    timeline.add([
      TweenMax.to("#conservatory", 300, { css: { opacity: 1 } }),
      TweenMax.to("#year_2010", 100, { css: { "font-weight": "800" } }),
    ]);

    const scene = this.$scrollmagic
      .scene({
        triggerElement: "#year_2010",
        triggerHook: 0.4,
        duration: 100,
      })
      .setPin("#year_2010")
      .setTween(timeline);

    this.$scrollmagic.addScene(scene);

    const timeline2 = new TimelineMax();
    timeline2.add([
      TweenMax.to("#conservatory", 100, { css: { opacity: 0 } }),
      TweenMax.to("#year_2010", 100, { css: { "font-weight": "700" } }),
    ]);

    const scene2 = this.$scrollmagic
      .scene({
        triggerElement: "#year_2016",
        triggerHook: 0.4,
        duration: 100,
      })
      .setTween(timeline2);

    this.$scrollmagic.addScene(scene2);
  }
}
