





















import { Component, Vue } from "vue-property-decorator";
import { Power0, TimelineMax, TweenMax } from "gsap";
import FESkills from "@/app/components/blocks/skills/FESkills.vue";
import BESkills from "@/app/components/blocks/skills/BESkills.vue";
import SoftSkills from "@/app/components/blocks/skills/SoftSkills.vue";

@Component({
  components: {
    FESkills,
    BESkills,
    SoftSkills,
  },
})
export default class Skills extends Vue {
  mounted() {
    const bezierPath = {
      left: {
        curviness: 1.25,
        autoRotate: true,
        values: [
          { x: 0, y: -50 },
          { x: 200, y: 10 },
          { x: 1300, y: -200 },
          { x: 1600, y: 100 },
        ],
      },
      right: {
        curviness: 1.25,
        autoRotate: -180,
        values: [
          { x: 0, y: -50 },
          { x: -200, y: 10 },
          { x: -1300, y: -200 },
          { x: -1600, y: 100 },
        ],
      },
    };
    const timeline = new TimelineMax();

    timeline.add([
      TweenMax.to(".fe", 500, { opacity: 1, delay: 400 }),
      TweenMax.to("#vue-bar", 500, { width: "30vw", delay: 800 }),
      TweenMax.to("#react-bar", 500, { width: "10vw", delay: 800 }),
      TweenMax.to("#angular-bar", 500, { width: "3vw", delay: 800 }),
      TweenMax.to(".slide-menu", 500, {
        x: "-104vw",
        ease: Power0.easeNone,
        delay: 1500,
      }),
      TweenMax.to(".be", 500, { opacity: 1, delay: 1900 }),
      TweenMax.to("#spring-bar", 500, { width: "15vw", delay: 2300 }),
      TweenMax.to("#quarkus-bar", 500, { width: "15vw", delay: 2300 }),
      TweenMax.to("#flask-bar", 500, { width: "4vw", delay: 2300 }),
      TweenMax.to("#asp-bar", 500, { width: "3vw", delay: 2300 }),
      TweenMax.to(".slide-menu", 500, {
        x: "-208vw",
        ease: Power0.easeNone,
        delay: 3000,
      }),
      TweenMax.to(".sk", 500, { opacity: 1, delay: 3400 }),
      TweenMax.to("#sk-1", 3000, { bezier: bezierPath.left, delay: 4000 }),
      TweenMax.to("#sk-2", 3000, { bezier: bezierPath.left, delay: 4000 }),
      TweenMax.to("#sk-3", 3000, { bezier: bezierPath.left, delay: 4000 }),
      TweenMax.to("#sk-4", 3000, { bezier: bezierPath.right, delay: 4000 }),
      TweenMax.to("#sk-5", 3000, { bezier: bezierPath.right, delay: 4000 }),
      TweenMax.to("#sk-6", 3000, { bezier: bezierPath.right, delay: 4000 }),
      TweenMax.to(".sk", 1000, { opacity: 0, delay: 6000 }),
    ]);

    const scene = this.$scrollmagic
      .scene({
        triggerElement: "#S3",
        triggerHook: 0.01,
        duration: 4000,
      })
      .setPin("#S3")
      .setTween(timeline);

    this.$scrollmagic.addScene(scene);
  }
}
