











import { Component, Vue } from "vue-property-decorator";
import { TweenMax } from "gsap";

@Component
export default class Timeline extends Vue {
  created() {
    window.onscroll = null;
  }
  mounted() {
    const scene = this.$scrollmagic
      .scene({
        triggerElement: "#S2",
        triggerHook: 1,
        duration: 1000,
      })
      .setTween(
        TweenMax.to("#time", 2000, {
          css: { position: "fixed", top: "40vh", opacity: 1 },
        })
      );

    this.$scrollmagic.addScene(scene);

    this.$scrollmagic.addScene(
      this.$scrollmagic
        .scene({
          triggerElement: "#S3",
          triggerHook: 1,
          duration: 400,
        })
        .setTween(TweenMax.to("#time", 100, { css: { opacity: 0 } }))
    );
  }
}
