




import { Component, Vue } from "vue-property-decorator";
import Homepage from "./app/views/Homepage.vue";
import { TimelineMax, TweenMax } from "gsap";

@Component({
  components: {
    Homepage,
  },
})
export default class App extends Vue {
  mounted() {
    const timeline = new TimelineMax();
    timeline.add([
      TweenMax.to("#S2", 100, { css: { opacity: 1 } }),
      TweenMax.to(".text", 10, { css: { opacity: 0 } }),
      TweenMax.to(".icons", 50, { opacity: 1 }),
      TweenMax.to(".scroll-message", 10, { opacity: 0 }),
      TweenMax.to(".image", 50, { opacity: 0 }),
      TweenMax.to(".header-content", 5, {
        opacity: 0,
        delay: 10,
      }),
      TweenMax.to("#goToHome", 10, {
        css: {
          delay: 50,
          top: "5vh",
          left: "80vw",
          animation: "wobble 3s infinite",
        },
      }),
      TweenMax.to("#goToTimeline", 10, {
        css: {
          delay: 50,
          top: "5vh",
          left: "85vw",
          "animation-delay": "0.5s",
          animation: "wobble 3s infinite",
        },
      }),
      TweenMax.to("#goToSkills", 10, {
        css: {
          delay: 50,
          top: "5vh",
          left: "90vw",
          "animation-delay": "1s",
          animation: "wobble 3s infinite",
        },
      }),
      TweenMax.to("#goToMore", 10, {
        css: {
          delay: 50,
          top: "5vh",
          left: "95vw",
          "animation-delay": "1.5s",
          animation: "wobble 3s infinite",
        },
      }),
      TweenMax.to(".language-selector", 10, {
        css: {
          top: "90vh",
          left: "93vw",
        },
      }),
    ]);

    const scene = this.$scrollmagic
      .scene({
        triggerElement: "#S2",
        triggerHook: 0.5,
        duration: 1000,
      })
      .setTween(timeline);

    this.$scrollmagic.addScene(scene);
  }
}
